import React, { useState, useEffect } from 'react';
import { Toast, Button, Badge, Collapse } from 'react-bootstrap';

const CookieBanner = ({ onNavigate }) => {
  const [show, setShow] = useState(false);
  const [preferences, setPreferences] = useState({
    essential: true, // Always true
    functional: false,
    analytics: false,
    performance: false
  });
  const [showDetails, setShowDetails] = useState(false);

  useEffect(() => {
    // Check if user has already made a choice
    const cookieConsent = localStorage.getItem('cookie-consent');
    if (!cookieConsent) {
      // Show banner after 2 seconds
      const timer = setTimeout(() => setShow(true), 2000);
      return () => clearTimeout(timer);
    }
  }, []);

  const handleAcceptAll = () => {
    const consent = {
      essential: true,
      functional: true,
      analytics: true,
      performance: true,
      timestamp: new Date().toISOString(),
      version: '2.0'
    };
    
    localStorage.setItem('cookie-consent', JSON.stringify(consent));
    setShow(false);
    
    // Initialize cookies based on consent
    initializeCookies(consent);
  };

  const handleAcceptSelected = () => {
    const consent = {
      ...preferences,
      timestamp: new Date().toISOString(),
      version: '2.0'
    };
    
    localStorage.setItem('cookie-consent', JSON.stringify(consent));
    setShow(false);
    
    // Initialize cookies based on consent
    initializeCookies(consent);
  };

  const handleRejectAll = () => {
    const consent = {
      essential: true, // Always required
      functional: false,
      analytics: false,
      performance: false,
      timestamp: new Date().toISOString(),
      version: '2.0'
    };
    
    localStorage.setItem('cookie-consent', JSON.stringify(consent));
    setShow(false);
    
    // Initialize only essential cookies
    initializeCookies(consent);
  };

  const initializeCookies = (consent) => {
    // This function would initialize the actual cookies based on user consent
    console.log('Initializing cookies with consent:', consent);
    
    // Example: Initialize Google Analytics only if analytics consent is given
    if (consent.analytics) {
      // Initialize Google Analytics
      console.log('Analytics cookies enabled');
    }
    
    if (consent.functional) {
      // Initialize functional cookies
      console.log('Functional cookies enabled');
    }
    
    if (consent.performance) {
      // Initialize performance cookies
      console.log('Performance cookies enabled');
    }
  };

  const handlePreferenceChange = (type) => {
    if (type === 'essential') return; // Essential cookies cannot be disabled
    
    setPreferences(prev => ({
      ...prev,
      [type]: !prev[type]
    }));
  };

  if (!show) return null;

  return (
    <div className="position-fixed bottom-0 start-0 end-0 p-3 bg-white border-top shadow-lg" style={{ zIndex: 1050 }}>
      <div className="container">
        <div className="row align-items-center">
          <div className="col-lg-8 mb-3 mb-lg-0">
            <div className="d-flex align-items-start">
              <div className="me-3 mt-1">
                <i className="bi bi-shield-check text-primary fs-4"></i>
              </div>
              <div>
                <h6 className="mb-2">
                  <i className="bi bi-cookie me-2"></i>
                  Cookie Consent - Your Privacy Matters
                </h6>
                <p className="text-muted mb-2 small">
                  We use cookies to enhance your experience, analyze site usage, and assist in our marketing efforts. 
                  By continuing to use our site, you consent to our use of cookies.
                </p>
                <div className="d-flex align-items-center gap-2 mb-2">
                  <Badge bg="success" className="small">POPIA Compliant</Badge>
                  <Button
                    variant="link"
                    size="sm"
                    className="p-0 text-decoration-none"
                    onClick={() => onNavigate('cookie-policy')}
                  >
                    Read Full Policy
                  </Button>
                  <Button
                    variant="link"
                    size="sm"
                    className="p-0 text-decoration-none"
                    onClick={() => setShowDetails(!showDetails)}
                  >
                    {showDetails ? 'Hide' : 'Show'} Details
                  </Button>
                </div>
                
                <Collapse in={showDetails}>
                  <div className="mt-3 p-3 bg-light rounded">
                    <h6 className="text-primary mb-3">Cookie Preferences</h6>
                    <div className="row">
                      <div className="col-md-6 mb-2">
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            checked={preferences.essential}
                            disabled
                            id="essential"
                          />
                          <label className="form-check-label small" htmlFor="essential">
                            <strong>Essential Cookies</strong> (Required)
                            <br />
                            <span className="text-muted">Authentication, security, basic functionality</span>
                          </label>
                        </div>
                      </div>
                      
                      <div className="col-md-6 mb-2">
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            checked={preferences.functional}
                            onChange={() => handlePreferenceChange('functional')}
                            id="functional"
                          />
                          <label className="form-check-label small" htmlFor="functional">
                            <strong>Functional Cookies</strong>
                            <br />
                            <span className="text-muted">Remember preferences, settings</span>
                          </label>
                        </div>
                      </div>
                      
                      <div className="col-md-6 mb-2">
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            checked={preferences.analytics}
                            onChange={() => handlePreferenceChange('analytics')}
                            id="analytics"
                          />
                          <label className="form-check-label small" htmlFor="analytics">
                            <strong>Analytics Cookies</strong>
                            <br />
                            <span className="text-muted">Usage statistics, site improvements</span>
                          </label>
                        </div>
                      </div>
                      
                      <div className="col-md-6 mb-2">
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            checked={preferences.performance}
                            onChange={() => handlePreferenceChange('performance')}
                            id="performance"
                          />
                          <label className="form-check-label small" htmlFor="performance">
                            <strong>Performance Cookies</strong>
                            <br />
                            <span className="text-muted">Site optimization, load balancing</span>
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </Collapse>
              </div>
            </div>
          </div>
          
          <div className="col-lg-4">
            <div className="d-flex gap-2 flex-wrap justify-content-lg-end">
              <Button
                variant="success"
                size="sm"
                onClick={handleAcceptAll}
                className="fw-semibold"
              >
                <i className="bi bi-check-all me-1"></i>
                Accept All
              </Button>
              
              <Button
                variant="primary"
                size="sm"
                onClick={handleAcceptSelected}
                className="fw-semibold"
              >
                <i className="bi bi-gear me-1"></i>
                Save Preferences
              </Button>
              
              <Button
                variant="outline-secondary"
                size="sm"
                onClick={handleRejectAll}
              >
                <i className="bi bi-x me-1"></i>
                Reject Optional
              </Button>
            </div>
            
            <div className="text-end mt-2">
              <small className="text-muted">
                <i className="bi bi-shield-lock me-1"></i>
                Your data is protected under POPIA
              </small>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CookieBanner;