import React from 'react';
import { Container, Row, Col, Card, Button, Badge } from 'react-bootstrap';

const Privacy = ({ onBack }) => {
  return (
    <div className="min-vh-100 bg-light">
      <Container className="py-5">
        {/* Header */}
        <Row className="mb-4">
          <Col>
            <div className="d-flex align-items-center mb-3">
              <Button 
                variant="outline-primary" 
                onClick={onBack}
                className="me-3"
              >
                <i className="bi bi-arrow-left me-2"></i>
                Back
              </Button>
              <h1 className="h2 mb-0">Privacy Policy</h1>
              <Badge bg="success" className="ms-3">POPIA Compliant</Badge>
            </div>
            <p className="text-muted">
              Last updated: {new Date().toLocaleDateString()} | 
              Compliant with the Protection of Personal Information Act (POPIA) of South Africa
            </p>
          </Col>
        </Row>

        <Row>
          <Col lg={10} xl={8}>
            <Card className="shadow-sm border-0">
              <Card.Body className="p-5">
                {/* Introduction */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">1. Introduction</h3>
                  <p>
                    Medi-Assist ("we," "our," or "us") is committed to protecting your privacy and personal information in accordance with the Protection of Personal Information Act (POPIA) and other applicable South African laws.
                  </p>
                  <p>
                    This Privacy Policy explains how we collect, use, disclose, and safeguard your personal information when you use our medical assistance platform and services.
                  </p>
                  <div className="alert alert-info">
                    <strong>Your Rights:</strong> Under POPIA, you have specific rights regarding your personal information. We are committed to upholding these rights and ensuring transparent data practices.
                  </div>
                </section>

                {/* Information We Collect */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">2. Information We Collect</h3>
                  
                  <h5 className="mb-3">2.1 Personal Information</h5>
                  <p>We may collect the following types of personal information:</p>
                  <ul>
                    <li><strong>Identity Information:</strong> Full name, ID number, date of birth</li>
                    <li><strong>Contact Information:</strong> Email address, phone number, physical address</li>
                    <li><strong>Account Information:</strong> Username, encrypted password, account preferences</li>
                    <li><strong>Demographic Information:</strong> Age, gender, language preference</li>
                  </ul>

                  <h5 className="mb-3">2.2 Health Information (Special Personal Information)</h5>
                  <div className="alert alert-warning">
                    <strong>Important:</strong> Health information is considered "special personal information" under POPIA and receives enhanced protection.
                  </div>
                  <ul>
                    <li><strong>Medical History:</strong> Previous conditions, surgeries, allergies</li>
                    <li><strong>Medication Information:</strong> Current medications, dosages, adherence data</li>
                    <li><strong>Appointment Data:</strong> Healthcare provider visits, appointment history</li>
                    <li><strong>Health Metrics:</strong> Vital signs, symptoms, health assessments</li>
                    <li><strong>Provider Communications:</strong> Messages with healthcare professionals</li>
                  </ul>

                  <h5 className="mb-3">2.3 Technical Information</h5>
                  <ul>
                    <li><strong>Device Information:</strong> Device type, operating system, browser</li>
                    <li><strong>Usage Data:</strong> Pages visited, features used, session duration</li>
                    <li><strong>Location Data:</strong> General location for emergency services (with consent)</li>
                    <li><strong>Log Data:</strong> IP address, access times, error logs</li>
                  </ul>
                </section>

                {/* Legal Basis for Processing */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">3. Legal Basis for Processing</h3>
                  <p>Under POPIA, we process your personal information based on the following lawful grounds:</p>
                  
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <div className="border rounded p-3 h-100">
                        <h6 className="text-primary">Consent</h6>
                        <p className="small mb-0">You have given explicit consent for specific processing activities, particularly for health information.</p>
                      </div>
                    </div>
                    <div className="col-md-6 mb-3">
                      <div className="border rounded p-3 h-100">
                        <h6 className="text-primary">Contract Performance</h6>
                        <p className="small mb-0">Processing necessary to perform our service contract with you.</p>
                      </div>
                    </div>
                    <div className="col-md-6 mb-3">
                      <div className="border rounded p-3 h-100">
                        <h6 className="text-primary">Legitimate Interest</h6>
                        <p className="small mb-0">For service improvement, security, and operational purposes (balanced against your privacy rights).</p>
                      </div>
                    </div>
                    <div className="col-md-6 mb-3">
                      <div className="border rounded p-3 h-100">
                        <h6 className="text-primary">Vital Interests</h6>
                        <p className="small mb-0">To protect your life or health in emergency situations.</p>
                      </div>
                    </div>
                  </div>
                </section>

                {/* How We Use Your Information */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">4. How We Use Your Information</h3>
                  <p>We use your personal information for the following purposes:</p>
                  
                  <h5 className="mb-3">4.1 Primary Service Functions</h5>
                  <ul>
                    <li>Providing medication tracking and reminder services</li>
                    <li>Managing healthcare appointments and schedules</li>
                    <li>Facilitating communication with healthcare providers</li>
                    <li>Maintaining your health records and medical history</li>
                    <li>Providing personalized health insights and recommendations</li>
                  </ul>

                  <h5 className="mb-3">4.2 Service Improvement and Support</h5>
                  <ul>
                    <li>Improving our platform functionality and user experience</li>
                    <li>Providing customer support and technical assistance</li>
                    <li>Conducting analytics to enhance service quality</li>
                    <li>Sending service-related notifications and updates</li>
                  </ul>

                  <h5 className="mb-3">4.3 Legal and Security Purposes</h5>
                  <ul>
                    <li>Complying with legal obligations and regulatory requirements</li>
                    <li>Protecting against fraud, security threats, and abuse</li>
                    <li>Enforcing our terms of service and policies</li>
                  </ul>
                </section>

                {/* Information Sharing */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">5. Information Sharing and Disclosure</h3>
                  
                  <div className="alert alert-success">
                    <strong>We do not sell personal information.</strong> We only share information as necessary to provide services and comply with legal requirements.
                  </div>

                  <h5 className="mb-3">5.1 Healthcare Providers</h5>
                  <p>With your explicit consent, we may share relevant health information with:</p>
                  <ul>
                    <li>Your designated healthcare providers</li>
                    <li>Specialists involved in your care</li>
                    <li>Pharmacies for medication management</li>
                  </ul>

                  <h5 className="mb-3">5.2 Service Providers</h5>
                  <p>We may share information with trusted third-party service providers who:</p>
                  <ul>
                    <li>Assist in providing our services (cloud hosting, technical support)</li>
                    <li>Help us improve our platform (analytics, research)</li>
                    <li>Are bound by strict confidentiality agreements</li>
                    <li>Comply with POPIA and our data protection standards</li>
                  </ul>

                  <h5 className="mb-3">5.3 Legal Requirements</h5>
                  <p>We may disclose information when required by:</p>
                  <ul>
                    <li>Court orders or legal processes</li>
                    <li>Law enforcement investigations</li>
                    <li>Public health authorities during emergencies</li>
                    <li>Regulatory bodies for compliance purposes</li>
                  </ul>
                </section>

                {/* Data Security */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">6. Data Security</h3>
                  <p>We implement comprehensive security measures to protect your personal information:</p>
                  
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <h6><i className="bi bi-shield-check text-success me-2"></i>Technical Safeguards</h6>
                      <ul className="small">
                        <li>End-to-end encryption for sensitive data</li>
                        <li>Secure data transmission (SSL/TLS)</li>
                        <li>Regular security updates and patches</li>
                        <li>Multi-factor authentication</li>
                      </ul>
                    </div>
                    <div className="col-md-6 mb-3">
                      <h6><i className="bi bi-people-fill text-info me-2"></i>Administrative Safeguards</h6>
                      <ul className="small">
                        <li>Access controls and user permissions</li>
                        <li>Employee training on data protection</li>
                        <li>Regular security audits and assessments</li>
                        <li>Incident response procedures</li>
                      </ul>
                    </div>
                    <div className="col-md-6 mb-3">
                      <h6><i className="bi bi-building text-warning me-2"></i>Physical Safeguards</h6>
                      <ul className="small">
                        <li>Secured data centers and server facilities</li>
                        <li>Environmental controls and monitoring</li>
                        <li>Restricted physical access to systems</li>
                        <li>Equipment disposal and destruction policies</li>
                      </ul>
                    </div>
                    <div className="col-md-6 mb-3">
                      <h6><i className="bi bi-clipboard-data text-primary me-2"></i>Compliance Measures</h6>
                      <ul className="small">
                        <li>Regular compliance assessments</li>
                        <li>Data protection impact assessments</li>
                        <li>Privacy by design implementation</li>
                        <li>Third-party security certifications</li>
                      </ul>
                    </div>
                  </div>
                </section>

                {/* Your POPIA Rights */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">7. Your Rights Under POPIA</h3>
                  <p>As a data subject under POPIA, you have the following rights:</p>
                  
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <div className="card border-primary">
                        <div className="card-header bg-primary text-white">
                          <strong>Right of Access</strong>
                        </div>
                        <div className="card-body">
                          You have the right to request confirmation of whether we process your personal information and to access that information.
                        </div>
                      </div>
                    </div>
                    
                    <div className="col-md-6 mb-3">
                      <div className="card border-info">
                        <div className="card-header bg-info text-white">
                          <strong>Right to Correction</strong>
                        </div>
                        <div className="card-body">
                          You can request correction or deletion of inaccurate, irrelevant, excessive, or unlawfully obtained personal information.
                        </div>
                      </div>
                    </div>

                    <div className="col-md-6 mb-3">
                      <div className="card border-warning">
                        <div className="card-header bg-warning text-dark">
                          <strong>Right to Object</strong>
                        </div>
                        <div className="card-body">
                          You may object to the processing of your personal information in certain circumstances, including direct marketing.
                        </div>
                      </div>
                    </div>

                    <div className="col-md-6 mb-3">
                      <div className="card border-success">
                        <div className="card-header bg-success text-white">
                          <strong>Right to Data Portability</strong>
                        </div>
                        <div className="card-body">
                          You can request your personal information in a structured, commonly used format for transfer to another service provider.
                        </div>
                      </div>
                    </div>
                  </div>
                </section>

                {/* Data Retention */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">8. Data Retention</h3>
                  <p>We retain your personal information for as long as necessary to:</p>
                  <ul>
                    <li>Provide our services to you</li>
                    <li>Comply with legal obligations (medical records: 6 years minimum)</li>
                    <li>Resolve disputes and enforce agreements</li>
                    <li>Support legitimate business purposes</li>
                  </ul>
                  <p>
                    When information is no longer needed, we securely delete or anonymize it in accordance with our data retention schedule and POPIA requirements.
                  </p>
                </section>

                {/* International Transfers */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">9. Cross-Border Data Transfers</h3>
                  <p>
                    If we transfer your personal information outside South Africa, we ensure appropriate safeguards are in place, including:
                  </p>
                  <ul>
                    <li>Adequate protection as determined by the Information Regulator</li>
                    <li>Binding corporate rules or standard contractual clauses</li>
                    <li>Your explicit consent for the transfer</li>
                    <li>Certification schemes or codes of conduct</li>
                  </ul>
                </section>

                {/* Contact and Complaints */}
                <section className="mb-5">
                  <h3 className="text-primary mb-3">10. Contact Information and Complaints</h3>
                  
                  <h5 className="mb-3">Information Officer</h5>
                  <div className="bg-light p-3 rounded mb-3">
                    <p className="mb-1"><strong>Name:</strong> Dr. Sarah Mbeki</p>
                    <p className="mb-1"><strong>Email:</strong> privacy@medi-assist.co.za</p>
                    <p className="mb-1"><strong>Phone:</strong> +27 21 123 4567</p>
                    <p className="mb-0"><strong>Address:</strong> 123 Health Street, Observatory, Cape Town, 7925</p>
                  </div>

                  <h5 className="mb-3">Complaints to the Information Regulator</h5>
                  <p>If you are unsatisfied with our response to your privacy concerns, you may lodge a complaint with:</p>
                  <div className="bg-light p-3 rounded">
                    <p className="mb-1"><strong>Information Regulator South Africa</strong></p>
                    <p className="mb-1"><strong>Email:</strong> inforeg@justice.gov.za</p>
                    <p className="mb-1"><strong>Phone:</strong> +27 12 406 4818</p>
                    <p className="mb-0"><strong>Website:</strong> www.justice.gov.za/inforeg</p>
                  </div>
                </section>

                {/* Updates to Policy */}
                <section className="mb-4">
                  <h3 className="text-primary mb-3">11. Updates to This Policy</h3>
                  <p>
                    We may update this Privacy Policy periodically to reflect changes in our practices, services, or applicable laws. We will notify you of significant changes through:
                  </p>
                  <ul>
                    <li>Email notifications to your registered address</li>
                    <li>Prominent notices on our platform</li>
                    <li>Updated version dates and change summaries</li>
                  </ul>
                </section>

                {/* Footer */}
                <div className="border-top pt-4 mt-5">
                  <div className="row">
                    <div className="col-md-8">
                      <p className="text-muted small">
                        <strong>Effective Date:</strong> {new Date().toLocaleDateString()}<br />
                        <strong>Version:</strong> 1.0<br />
                        <strong>Compliance:</strong> This Privacy Policy complies with the Protection of Personal Information Act (Act 4 of 2013), the Consumer Protection Act, and applicable South African healthcare regulations.
                      </p>
                    </div>
                    <div className="col-md-4 text-end">
                      <Badge bg="success" className="mb-2">POPIA Compliant</Badge><br />
                      <Badge bg="info">Healthcare Certified</Badge>
                    </div>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Privacy;