import React, { useState } from 'react';
import { Container, Row, Col, Card, Form, Button, Alert, Spinner } from 'react-bootstrap';

const Contact = ({ onBack }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
    contactType: 'general'
  });
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    if (error) setError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    
    // Basic validation
    if (!formData.name || !formData.email || !formData.message) {
      setError('Please fill in all required fields.');
      setLoading(false);
      return;
    }

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      console.log('Contact form submitted:', formData);
      setSuccess(true);
      setFormData({
        name: '',
        email: '',
        subject: '',
        message: '',
        contactType: 'general'
      });
    } catch (err) {
      setError('Failed to send message. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-vh-100 bg-light">
      <Container className="py-5">
        {/* Header */}
        <Row className="mb-4">
          <Col>
            <div className="d-flex align-items-center mb-3">
              <Button 
                variant="outline-primary" 
                onClick={onBack}
                className="me-3"
              >
                <i className="bi bi-arrow-left me-2"></i>
                Back
              </Button>
              <h1 className="h2 mb-0">Contact Us</h1>
            </div>
            <p className="text-muted">Get in touch with our team - we're here to help</p>
          </Col>
        </Row>

        <Row>
          {/* Contact Form */}
          <Col lg={8}>
            <Card className="shadow-sm border-0">
              <Card.Body className="p-4">
                <h3 className="text-primary mb-4">Send us a Message</h3>
                
                {success && (
                  <Alert variant="success" className="mb-4">
                    <i className="bi bi-check-circle-fill me-2"></i>
                    <strong>Message sent successfully!</strong> We'll get back to you within 24 hours.
                  </Alert>
                )}

                {error && (
                  <Alert variant="danger" className="mb-4">
                    <i className="bi bi-exclamation-triangle-fill me-2"></i>
                    {error}
                  </Alert>
                )}

                <Form onSubmit={handleSubmit}>
                  <Row>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Full Name *</Form.Label>
                        <Form.Control
                          type="text"
                          name="name"
                          value={formData.name}
                          onChange={handleChange}
                          placeholder="Enter your full name"
                          required
                        />
                      </Form.Group>
                    </Col>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Email Address *</Form.Label>
                        <Form.Control
                          type="email"
                          name="email"
                          value={formData.email}
                          onChange={handleChange}
                          placeholder="your.email@example.com"
                          required
                        />
                      </Form.Group>
                    </Col>
                  </Row>

                  <Row>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Contact Type</Form.Label>
                        <Form.Select
                          name="contactType"
                          value={formData.contactType}
                          onChange={handleChange}
                        >
                          <option value="general">General Inquiry</option>
                          <option value="support">Technical Support</option>
                          <option value="partnership">Partnership</option>
                          <option value="feedback">Feedback</option>
                          <option value="billing">Billing</option>
                        </Form.Select>
                      </Form.Group>
                    </Col>
                    <Col md={6}>
                      <Form.Group className="mb-3">
                        <Form.Label>Subject</Form.Label>
                        <Form.Control
                          type="text"
                          name="subject"
                          value={formData.subject}
                          onChange={handleChange}
                          placeholder="Brief description of your inquiry"
                        />
                      </Form.Group>
                    </Col>
                  </Row>

                  <Form.Group className="mb-4">
                    <Form.Label>Message *</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={6}
                      name="message"
                      value={formData.message}
                      onChange={handleChange}
                      placeholder="Please provide details about your inquiry..."
                      required
                    />
                  </Form.Group>

                  <Button
                    type="submit"
                    variant="primary"
                    size="lg"
                    disabled={loading}
                    className="w-100"
                  >
                    {loading ? (
                      <>
                        <Spinner animation="border" size="sm" className="me-2" />
                        Sending Message...
                      </>
                    ) : (
                      <>
                        <i className="bi bi-send me-2"></i>
                        Send Message
                      </>
                    )}
                  </Button>
                </Form>
              </Card.Body>
            </Card>
          </Col>

          {/* Contact Information */}
          <Col lg={4}>
            <Card className="shadow-sm border-0 mb-4">
              <Card.Body>
                <h4 className="text-primary mb-4">Get in Touch</h4>
                
                <div className="mb-4">
                  <h6 className="mb-2">
                    <i className="bi bi-geo-alt text-primary me-2"></i>
                    Office Address
                  </h6>
                  <p className="text-muted small mb-0">
                    123 Health Street<br />
                    Observatory, Cape Town<br />
                    Western Cape, 7925<br />
                    South Africa
                  </p>
                </div>

                <div className="mb-4">
                  <h6 className="mb-2">
                    <i className="bi bi-telephone text-primary me-2"></i>
                    Phone Numbers
                  </h6>
                  <p className="text-muted small mb-1">
                    <strong>Main:</strong> +27 21 123 4567
                  </p>
                  <p className="text-muted small mb-0">
                    <strong>Support:</strong> +27 21 123 4568
                  </p>
                </div>

                <div className="mb-4">
                  <h6 className="mb-2">
                    <i className="bi bi-envelope text-primary me-2"></i>
                    Email Addresses
                  </h6>
                  <p className="text-muted small mb-1">
                    <strong>General:</strong> info@medi-assist.co.za
                  </p>
                  <p className="text-muted small mb-0">
                    <strong>Support:</strong> support@medi-assist.co.za
                  </p>
                </div>

                <div className="mb-4">
                  <h6 className="mb-2">
                    <i className="bi bi-clock text-primary me-2"></i>
                    Business Hours
                  </h6>
                  <p className="text-muted small mb-1">
                    <strong>Monday - Friday:</strong> 8:00 AM - 6:00 PM
                  </p>
                  <p className="text-muted small mb-1">
                    <strong>Saturday:</strong> 9:00 AM - 2:00 PM
                  </p>
                  <p className="text-muted small mb-0">
                    <strong>Sunday:</strong> Closed
                  </p>
                </div>
              </Card.Body>
            </Card>

            <Card className="shadow-sm border-0">
              <Card.Body>
                <h5 className="text-primary mb-3">Emergency Support</h5>
                <p className="text-muted small mb-3">
                  For urgent technical issues affecting patient care, contact our 24/7 emergency support line.
                </p>
                <Button variant="outline-danger" size="sm" className="w-100">
                  <i className="bi bi-telephone-fill me-2"></i>
                  Emergency: +27 86 000 0000
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Contact;