import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Form, Alert, Table, Badge } from 'react-bootstrap';

const CookieSettings = ({ onBack }) => {
  const [preferences, setPreferences] = useState({
    essential: true,
    functional: false,
    analytics: false,
    performance: false
  });
  
  const [currentConsent, setCurrentConsent] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);
  const [cookies, setCookies] = useState([]);

  useEffect(() => {
    // Load current cookie consent
    const consent = localStorage.getItem('cookie-consent');
    if (consent) {
      const parsed = JSON.parse(consent);
      setCurrentConsent(parsed);
      setPreferences({
        essential: parsed.essential,
        functional: parsed.functional,
        analytics: parsed.analytics,
        performance: parsed.performance
      });
    }
    
    // Get all cookies (simulated - in real app would read from document.cookie)
    loadCurrentCookies();
  }, []);

  const loadCurrentCookies = () => {
    // Simulated cookie data - in real app would parse document.cookie
    const mockCookies = [
      { name: 'session_id', value: 'abc123...', type: 'essential', expires: 'Session', size: '32 bytes' },
      { name: 'auth_token', value: 'jwt.token...', type: 'essential', expires: '30 days', size: '256 bytes' },
      { name: 'csrf_token', value: 'csrf123...', type: 'essential', expires: '1 hour', size: '64 bytes' },
      { name: 'user_lang', value: 'en', type: 'functional', expires: '1 year', size: '8 bytes' },
      { name: 'dashboard_view', value: 'grid', type: 'functional', expires: '6 months', size: '16 bytes' },
      { name: '_ga', value: 'GA1.1.123...', type: 'analytics', expires: '2 years', size: '128 bytes' },
      { name: '_gid', value: 'GA1.1.456...', type: 'analytics', expires: '24 hours', size: '128 bytes' },
      { name: 'load_balancer', value: 'server_1', type: 'performance', expires: '1 hour', size: '24 bytes' }
    ];
    
    setCookies(mockCookies);
  };

  const handlePreferenceChange = (type) => {
    if (type === 'essential') return; // Cannot disable essential cookies
    
    setPreferences(prev => ({
      ...prev,
      [type]: !prev[type]
    }));
  };

  const handleSavePreferences = () => {
    const newConsent = {
      ...preferences,
      timestamp: new Date().toISOString(),
      version: '2.0'
    };
    
    localStorage.setItem('cookie-consent', JSON.stringify(newConsent));
    setCurrentConsent(newConsent);
    setShowSuccess(true);
    
    // Clear cookies based on preferences
    clearDisabledCookies();
    
    // Hide success message after 3 seconds
    setTimeout(() => setShowSuccess(false), 3000);
  };

  const clearDisabledCookies = () => {
    // In real app, would actually delete cookies from document.cookie
    console.log('Clearing disabled cookies based on preferences:', preferences);
    
    // Simulate clearing cookies
    const filteredCookies = cookies.filter(cookie => {
      if (cookie.type === 'essential') return true;
      return preferences[cookie.type];
    });
    
    setCookies(filteredCookies);
  };

  const handleClearAllCookies = () => {
    if (window.confirm('Are you sure you want to clear all non-essential cookies? This will log you out.')) {
      // Clear all non-essential cookies
      const essentialOnly = {
        essential: true,
        functional: false,
        analytics: false,
        performance: false,
        timestamp: new Date().toISOString(),
        version: '2.0'
      };
      
      localStorage.setItem('cookie-consent', JSON.stringify(essentialOnly));
      setPreferences(essentialOnly);
      setCurrentConsent(essentialOnly);
      
      // Filter cookies to essential only
      const essentialCookies = cookies.filter(cookie => cookie.type === 'essential');
      setCookies(essentialCookies);
      
      setShowSuccess(true);
      setTimeout(() => setShowSuccess(false), 3000);
    }
  };

  const downloadCookieReport = () => {
    const report = {
      consent: currentConsent,
      cookies: cookies,
      preferences: preferences,
      generated: new Date().toISOString(),
      userAgent: navigator.userAgent
    };
    
    const blob = new Blob([JSON.stringify(report, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `cookie-report-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const getCookieTypeColor = (type) => {
    switch (type) {
      case 'essential': return 'danger';
      case 'functional': return 'warning';
      case 'analytics': return 'info';
      case 'performance': return 'secondary';
      default: return 'light';
    }
  };

  const getCookieTypeCount = (type) => {
    return cookies.filter(cookie => cookie.type === type).length;
  };

  return (
    <div className="min-vh-100 bg-light">
      <Container className="py-5">
        {/* Header */}
        <Row className="mb-4">
          <Col>
            <div className="d-flex align-items-center mb-3">
              <Button 
                variant="outline-primary" 
                onClick={onBack}
                className="me-3"
              >
                <i className="bi bi-arrow-left me-2"></i>
                Back
              </Button>
              <h1 className="h2 mb-0">Cookie Settings</h1>
              <Badge bg="primary" className="ms-3">Manage Your Privacy</Badge>
            </div>
            <p className="text-muted">Customize your cookie preferences and view active cookies</p>
          </Col>
        </Row>

        {/* Success Alert */}
        {showSuccess && (
          <Row className="mb-4">
            <Col>
              <Alert variant="success" className="d-flex align-items-center">
                <i className="bi bi-check-circle-fill me-2"></i>
                Your cookie preferences have been saved successfully!
              </Alert>
            </Col>
          </Row>
        )}

        <Row>
          {/* Cookie Preferences */}
          <Col lg={8} className="mb-4">
            <Card className="shadow-sm border-0 mb-4">
              <Card.Body>
                <h3 className="text-primary mb-4">
                  <i className="bi bi-gear-fill me-2"></i>
                  Cookie Preferences
                </h3>
                
                <Form>
                  {/* Essential Cookies */}
                  <div className="border rounded p-3 mb-3 bg-danger bg-opacity-10">
                    <Form.Check 
                      type="switch"
                      id="essential-switch"
                      checked={preferences.essential}
                      disabled
                      className="mb-2"
                      label={
                        <div>
                          <strong className="text-danger">Essential Cookies (Required)</strong>
                          <Badge bg="danger" className="ms-2">{getCookieTypeCount('essential')}</Badge>
                        </div>
                      }
                    />
                    <p className="text-muted small mb-0">
                      These cookies are necessary for the website to function properly. 
                      They enable core functionality such as user authentication, security, and basic site operations.
                    </p>
                  </div>

                  {/* Functional Cookies */}
                  <div className="border rounded p-3 mb-3">
                    <Form.Check 
                      type="switch"
                      id="functional-switch"
                      checked={preferences.functional}
                      onChange={() => handlePreferenceChange('functional')}
                      className="mb-2"
                      label={
                        <div>
                          <strong className="text-warning">Functional Cookies</strong>
                          <Badge bg="warning" className="ms-2 text-dark">{getCookieTypeCount('functional')}</Badge>
                        </div>
                      }
                    />
                    <p className="text-muted small mb-0">
                      These cookies enhance your user experience by remembering your preferences, 
                      language settings, and customizations.
                    </p>
                  </div>

                  {/* Analytics Cookies */}
                  <div className="border rounded p-3 mb-3">
                    <Form.Check 
                      type="switch"
                      id="analytics-switch"
                      checked={preferences.analytics}
                      onChange={() => handlePreferenceChange('analytics')}
                      className="mb-2"
                      label={
                        <div>
                          <strong className="text-info">Analytics Cookies</strong>
                          <Badge bg="info" className="ms-2">{getCookieTypeCount('analytics')}</Badge>
                        </div>
                      }
                    />
                    <p className="text-muted small mb-0">
                      These cookies help us understand how visitors use our website, 
                      which pages are most popular, and how we can improve our services.
                    </p>
                  </div>

                  {/* Performance Cookies */}
                  <div className="border rounded p-3 mb-3">
                    <Form.Check 
                      type="switch"
                      id="performance-switch"
                      checked={preferences.performance}
                      onChange={() => handlePreferenceChange('performance')}
                      className="mb-2"
                      label={
                        <div>
                          <strong className="text-secondary">Performance Cookies</strong>
                          <Badge bg="secondary" className="ms-2">{getCookieTypeCount('performance')}</Badge>
                        </div>
                      }
                    />
                    <p className="text-muted small mb-0">
                      These cookies help improve the performance and speed of our website 
                      by optimizing loading times and server resources.
                    </p>
                  </div>
                </Form>

                <div className="d-flex gap-2 mt-4">
                  <Button 
                    variant="primary" 
                    onClick={handleSavePreferences}
                    className="fw-semibold"
                  >
                    <i className="bi bi-check-lg me-2"></i>
                    Save Preferences
                  </Button>
                  <Button 
                    variant="outline-warning" 
                    onClick={handleClearAllCookies}
                  >
                    <i className="bi bi-trash me-2"></i>
                    Clear All Optional
                  </Button>
                </div>
              </Card.Body>
            </Card>

            {/* Active Cookies */}
            <Card className="shadow-sm border-0">
              <Card.Body>
                <div className="d-flex justify-content-between align-items-center mb-4">
                  <h3 className="text-primary mb-0">
                    <i className="bi bi-list-ul me-2"></i>
                    Active Cookies
                  </h3>
                  <Button 
                    variant="outline-primary" 
                    size="sm"
                    onClick={downloadCookieReport}
                  >
                    <i className="bi bi-download me-2"></i>
                    Download Report
                  </Button>
                </div>
                
                <div className="table-responsive">
                  <Table striped>
                    <thead className="bg-light">
                      <tr>
                        <th>Cookie Name</th>
                        <th>Type</th>
                        <th>Value</th>
                        <th>Expires</th>
                        <th>Size</th>
                      </tr>
                    </thead>
                    <tbody>
                      {cookies.map((cookie, index) => (
                        <tr key={index}>
                          <td>
                            <code className="text-dark">{cookie.name}</code>
                          </td>
                          <td>
                            <Badge bg={getCookieTypeColor(cookie.type)}>
                              {cookie.type}
                            </Badge>
                          </td>
                          <td>
                            <span className="text-muted small">
                              {cookie.value.length > 20 ? 
                                `${cookie.value.substring(0, 20)}...` : 
                                cookie.value
                              }
                            </span>
                          </td>
                          <td className="text-muted small">{cookie.expires}</td>
                          <td className="text-muted small">{cookie.size}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
                
                {cookies.length === 0 && (
                  <div className="text-center text-muted py-4">
                    <i className="bi bi-cookie fs-1 mb-2"></i>
                    <p>No cookies are currently active</p>
                  </div>
                )}
              </Card.Body>
            </Card>
          </Col>

          {/* Sidebar */}
          <Col lg={4}>
            {/* Current Consent Status */}
            <Card className="shadow-sm border-0 mb-4">
              <Card.Body>
                <h5 className="text-primary mb-3">Consent Status</h5>
                
                {currentConsent ? (
                  <div>
                    <p className="mb-2">
                      <strong>Last Updated:</strong><br />
                      <span className="text-muted small">
                        {new Date(currentConsent.timestamp).toLocaleString()}
                      </span>
                    </p>
                    <p className="mb-2">
                      <strong>Policy Version:</strong> {currentConsent.version}
                    </p>
                    
                    <div className="mt-3">
                      <h6>Active Categories:</h6>
                      {Object.entries(currentConsent).map(([key, value]) => {
                        if (key === 'timestamp' || key === 'version') return null;
                        return (
                          <div key={key} className="mb-1">
                            <Badge 
                              bg={value ? 'success' : 'secondary'} 
                              className="me-2 text-capitalize"
                            >
                              {key}
                            </Badge>
                            {value ? 'Enabled' : 'Disabled'}
                          </div>
                        );
                      })}
                    </div>
                  </div>
                ) : (
                  <div className="text-center text-muted">
                    <i className="bi bi-exclamation-triangle fs-1 mb-2"></i>
                    <p>No consent recorded</p>
                  </div>
                )}
              </Card.Body>
            </Card>

            {/* Quick Actions */}
            <Card className="shadow-sm border-0 mb-4">
              <Card.Body>
                <h5 className="text-primary mb-3">Quick Actions</h5>
                
                <div className="d-grid gap-2">
                  <Button variant="outline-success" size="sm">
                    <i className="bi bi-check-all me-2"></i>
                    Accept All Cookies
                  </Button>
                  
                  <Button variant="outline-danger" size="sm">
                    <i className="bi bi-x-circle me-2"></i>
                    Reject All Optional
                  </Button>
                  
                  <Button variant="outline-warning" size="sm">
                    <i className="bi bi-arrow-clockwise me-2"></i>
                    Reset to Defaults
                  </Button>
                </div>
              </Card.Body>
            </Card>

            {/* Help & Information */}
            <Card className="shadow-sm border-0">
              <Card.Body>
                <h5 className="text-primary mb-3">Need Help?</h5>
                
                <p className="small text-muted mb-3">
                  Learn more about how we use cookies and protect your privacy.
                </p>
                
                <div className="d-grid gap-2">
                  <Button 
                    variant="outline-info" 
                    size="sm"
                    onClick={() => window.open('/cookie-policy', '_blank')}
                  >
                    <i className="bi bi-book me-2"></i>
                    Cookie Policy
                  </Button>
                  
                  <Button 
                    variant="outline-info" 
                    size="sm"
                    onClick={() => window.open('/privacy', '_blank')}
                  >
                    <i className="bi bi-shield-check me-2"></i>
                    Privacy Policy
                  </Button>
                  
                  <Button 
                    variant="outline-info" 
                    size="sm"
                    href="mailto:privacy@medi-assist.co.za"
                  >
                    <i className="bi bi-envelope me-2"></i>
                    Contact Privacy Team
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default CookieSettings;